.contact-main-container {
  margin: 5% auto;
  display: flex;
  width: 60%;
}
.left-container-contact {
  width: 40%;
  background: aliceblue;
  border-bottom-left-radius: 30px;
}
.right-container-contact {
  padding: 40px;
  background: #fff;
  border-top-right-radius: 30px;
}
.icons-text {
  line-height: 2em;
  display: flex;
  padding: 10px;
  justify-content: space-between;
}
.header-texts {
  width: 80%;
  margin: 5% auto;
  line-height: 2em;
}
.left-area-content {
  padding: 20px;
}
.left-icon-info {
  background: brown;
  padding: 20px;
  margin-left: 15%;
  height: 7vh;
  border-radius: 70%;
}
.right-text-info {
  margin-right: 10%;
}
.left-icon-info h3 {
  text-align: center;
  margin: 10%;
  color: #f9f9f9;
}
.form-main {
  display: flex;
  justify-content: space-between;
}
.form-email {
  margin: 0 3%;
}
#contact-us-form {
  line-height: 3em;
  overflow: hidden;
}
#text {
  min-width: 25vw;
  height: 4em;
  font-size: 1em;
  border: none;
  border-bottom: 1px solid #171717;
  transform: translateX(-50%);
  opacity: 0;
  transition: all 1s ease-in;
  padding: 5px;
}
#text:focus {
  outline: none;
  transform: translateX(0%);
  opacity: 1;
}

#email {
  min-width: 25vw;
  height: 4em;
  font-size: 1em;
  border: none;
  border-bottom: 1px solid #171717;
  transform: translateX(-50%);
  opacity: 0;
  transition: all 1s ease-in;
  padding: 5px;
}
#email:focus {
  outline: none;
  transform: translateX(0%);
  opacity: 1;
}
#texxt {
  min-width: 25vw;
  height: 4em;
  font-size: 1em;
  border: none;
  border-bottom: 1px solid #171717;
  transform: translateX(-50%);
  opacity: 0;
  transition: all 1s ease-in;
  padding: 5px;
}
#texxt:focus {
  outline: none;
  transform: translateX(0%);
  opacity: 1;
}
.random-div-one {
  min-width: 25vw;
  border-top: 1px solid #f1f1f1;
}
.random-div-two {
  min-width: 25vw;
  border-top: 1px solid #f1f1f1;
}
.random-div-three {
  min-width: 25vw;
  border-top: 1px solid #f1f1f1;
}
.random-div-four {
  margin-top: -20px;
  min-width: 25vw;
  border-bottom: 1px solid #f1f1f1;
}
