textarea {
  width: 25vw;
  border: none;
  border-bottom: 1px solid #171717;
  transform: translateX(-50%);
  opacity: 0;
  transition: all 1s ease-in;
  padding: 5px;
}
textarea:focus {
  outline: none;
  transform: translateX(0%);
  opacity: 1;
}
.button-contact {
  margin: 10% 0;
}
#button-section {
  padding: 20px;
  border: 1px solid black;
  text-decoration: none;
  color: #000;
}
#button-section:hover {
  color: #fff;
  background: #000;
  transition: 0.9s ease-in-out;
}
