.main-area-sec {
  margin: 5%;
  display: flex;
  justify-content: center;
}
.main-donation-contain {
  background-color: #fff;
  box-shadow: 0 0 45px #f9f9f9;
  padding: 40px;
}
#donation-main-form {
  line-height: 5vh;
}
#firstname {
  min-width: 25vw;
  height: 4em;
  font-size: 1em;
  padding: 10px;
}
#lastname {
  min-width: 25vw;
  height: 4em;
  font-size: 1em;
  padding: 10px;
}
#address {
  min-width: 25vw;
  height: 4em;
  font-size: 1em;
  padding: 10px;
}
#zip {
  min-width: 25vw;
  height: 4em;
  font-size: 1em;
  padding: 10px;
}
#number {
  min-width: 25vw;
  height: 4em;
  font-size: 1em;
  padding: 10px;
}
.donate-button {
  margin: 8% 0;
}
#donation-submit {
  border: 1px solid black;
  text-decoration: none;
  color: #000;
  padding: 20px;
}
.image-main-donation {
  background: url("../assest/coat.jpg");
  background-position: center;
  background-repeat: no-repeat;
  /* background-attachment: fixed; */
  background-size: cover;
  height: 90vh;
  width: 40vw;
  color: #f9f9f9;
  text-align: center;
  line-height: 2em;
}
.image-main-donation h4 {
  text-align: justify;
  width: 80%;
  margin: auto;
}
