.content-container {
  display: flex;
  justify-content: center;
}
.image-section-first {
  background: url("../assest/coat.jpg");
  height: 20vh;
  width: 480px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.image-section-mid {
  background: url("../assest/monk-praying.jpg");
  height: 20vh;
  width: 480px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.image-section-bottom {
  background: url("../assest/spidy.jpg");
  height: 20vh;
  width: 480px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.first {
  padding: 20px;
  line-height: 1.5em;
  text-align: justify;
}
.left-div {
  border: 1px solid #c3c3c3; 
  padding: 30px;
}
#h4-pro {
  text-align: center;
  margin: 2% 0;
  color: #000;
}
.text-section-bottom {
  width: 480px;
  color: #c8c8c8;
  padding: 10px;
}
.button {
  margin: 1% 0;
}
.button #link-readmore {
  text-decoration: none;
  color: #000;
  padding: 10px;
  border: 1px solid black;
}
.button #link-readmore:hover {
  background: #171717;
  color: #fff;
  transition: 2s ease-out;
}
.parent-container h1 {
  text-align: center;
  margin: 3% 0;
}
