@import url("https://fonts.googleapis.com/css?family=Patrick+Hand+SC|Roboto&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Allison&family=Indie+Flower&family=Quicksand:wght@300;700&display=swap");
.text-only-container {
  display: flex;
}
.left-container-text {
  width: 50%;
  margin: 40px;
}

.img-sec {
  background: url("../assest/coat.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 35vw;
  height: 40vh;
}
.img-sec-one {
  background: url("../assest/spidy.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 35vw;
  margin: -30% 10%;
  height: 40vh;
}
.img-sec-two {
  background: url("../assest/monk-praying.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 35vw;
  margin: 5% 20%;
  height: 40vh;
}
.right-container-text {
  width: 50%;
}
.text-right-section {
  padding: 60px;
  margin: 10% 0 0 0;
}
.content-1 {
  width: 80%;
  line-height: 1.8em;
  text-align: justify;
  font-size: 1.5em;
  font-family: "Allison", cursive;
  font-family: "Indie Flower", cursive;
  font-family: "Quicksand", sans-serif;
}
#simple {
  font-family: "Patrick Hand SC", cursive;
}

.link-only {
  margin-top: 5%;
}
#react-icons-used {
  text-decoration: none;
  color: #000;
  padding: 10px;
  justify-content: center;
  text-align: center;
  border: 1px solid #c7c7c7;
}
#react-icons-used:hover{
    background: #171717;
    color: #fff;
    box-shadow: 2px 5px 20px #171717;
    transition: 2s ease-out;
}