.first-sec-don {
  padding: 40px;
  margin: 2% auto;
  background: #fff;
  display: flex;
  justify-content: space-evenly;
}
.some-text-don{
    margin: 20px 0; 
    width: 50%;
    text-align: justify;
}
.image-don-section {
  background: url("../assest/donation-img.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  height: 40vh;
  width: 20vw;
}
.only-border{
    border:1px solid #f3f3f3;
}
.button-div-don {
  margin: 10% auto;
}
#donation-head {
  border: 1px solid black;
  padding: 20px;
  text-decoration: none;
  color: #000;
}
#donation-head:hover {
    color: #fff;
    background: #000;
    transition: .9s ease-in;
  }