@import url("https://fonts.googleapis.com/css2?family=Shadows+Into+Light&display=swap");
body {
  margin: 0;
}
div#slider {
  overflow: hidden;
}
#slider-image:nth-child(1) {
  background: url("../assest/hearing.jpg");
}
#slider-image:nth-child(2) {
  background: url("../assest/monk-praying.jpg");
}
#slider-image:nth-child(3) {
  background: url("../assest/playing-water.jpg");
}
#slider-image:nth-child(4) {
  background: url("../assest/reading.jpg");
}
#slider-image:nth-child(5) {
  background: url("../assest/temple-banner.jpg");
}
div#slider figure #slider-image {
  width: 20%;
  float: left;
  min-height: 70vh;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}
div#slider figure {
  position: relative;
  width: 500%;
  height: 30vh;
  margin: 0;
  left: 0;
  text-align: left;
  font-size: 0;
  animation: 30s slidy infinite;
}
@keyframes slidy {
  0% {
    left: 0%;
  }
  20% {
    left: 0%;
  }
  25% {
    left: -100%;
  }
  45% {
    left: -100%;
  }
  50% {
    left: -200%;
  }
  70% {
    left: -200%;
  }
  75% {
    left: -300%;
  }
  95% {
    left: -300%;
  }
  100% {
    left: -400%;
  }
}
.about-container-main {
  text-align: center;
  padding: 20px;
}
.absolute-container {
  position: absolute;
  z-index: 1;
  margin: 10% 40%;
  font-size: 3.5em;
  letter-spacing: 0.2em;
  font-family: "Shadows Into Light", cursive;
  color: #f9f9f9;
  text-shadow: 2px 3px 10px #000;
}
.about-us-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  line-height: 1.7em;
}
.content-left {
  width: 80%;
  text-align: left;
  padding: 50px;
  line-height: 2em;
}
.right-content-about {
  padding: 5px;
  text-align: left;
  border-left: 2px solid rgb(116, 116, 116);
}
.faq-heading {
  border-bottom: #777;
  padding: 10px 60px;
}
.faq-container {
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.hr-line {
  width: 40%;
  margin: auto;
}
/* Style the buttons that are used to open and close the faq-page body */
.faq-page {
  /* background-color: #eee; */
  color: #444;
  cursor: pointer;
  padding: 30px 20px;
  width: 60%;
  border: none;
  outline: none;
  transition: 0.4s;
  margin: auto;
}
.faq-body {
  margin: auto;
  padding: 30px 20px;
  line-height: 2em;
  text-align: justify;
  width: 60%;
  padding: auto;
}

/* Add a background color to the button if it is clicked on (add the .active class with JS), and when you move the mouse over it (hover) */
.active,
.faq-page:hover {
  background-color: #f9f9f9;
}

/* Style the faq-page panel. Note: hidden by default */
.faq-body {
  padding: 0 18px;
  background-color: white;
  display: none;
  overflow: hidden;
}

.faq-page:after {
  content: "\02795";
  font-size: 13px;
  color: #777;
  float: right;
  margin-left: 5px;
}

.active:after {
  content: "\2796";
}
.award-section-main {
  display: flex;
  justify-content: space-around;
  padding: 20px;
  height: 30vh;
  background-image: linear-gradient(
      90deg,
      rgba(31, 156, 231, 0.6951155462184874) 0%,
      rgba(32, 108, 227, 0.6278886554621849) 45%,
      rgba(48, 142, 217, 0.44021358543417366) 83%
    ),
    url("../assest/coat.jpg");

  width: 100%;
  background-position: center;
  /* background-attachment: fixed; */
}
.award-section-main h2 {
  margin: 7vh auto;
  padding: 40px;
  color: #f9f9f9;
  border: 2px solid #f5f5f5;
}
.award-section-main span {
  border-bottom: 2px solid #a9a9a9;
  line-height: 2em;
}

