div#slider {
  overflow: hidden;
}
#slider-image:nth-child(1) {
  background: url(/static/media/hearing.0bb13c3e.jpg);
}
#slider-image:nth-child(2) {
  background: url(/static/media/monk-praying.71280f87.jpg);
}
#slider-image:nth-child(3) {
  background: url(/static/media/playing-water.cb9d3dd8.jpg);
}
#slider-image:nth-child(4) {
  background: url(/static/media/reading.0bb13c3e.jpg);
}
#slider-image:nth-child(5) {
  background: url(/static/media/temple-banner.37f0b620.jpg);
}
div#slider figure #slider-image {
  width: 20%;
  float: left;
  min-height: 70vh;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}
div#slider figure {
  position: relative;
  width: 500%;
  height: 30vh;
  margin: 0;
  left: 0;
  text-align: left;
  font-size: 0;
  animation: 30s slidy infinite;
}
@keyframes slidy {
  0% {
    left: 0%;
  }
  20% {
    left: 0%;
  }
  25% {
    left: -100%;
  }
  45% {
    left: -100%;
  }
  50% {
    left: -200%;
  }
  70% {
    left: -200%;
  }
  75% {
    left: -300%;
  }
  95% {
    left: -300%;
  }
  100% {
    left: -400%;
  }
}
