.gallary-start-div {
  display: flex;
  justify-content: space-evenly;
  margin: 10px 0;
}
.first-img {
  background: linear-gradient(
    to right,
    rgba(56, 58, 144, 0.7),
    rgba(56, 58, 96, 0.95)
  );
  width: 30vw;
  height: 40vh;
  background-image: url("../assest/monk-praying.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.first-img h2 {
  color: #fff;
  text-align: center;
  margin-top: 30%;
}
.first-img:hover {
  /* background: linear-gradient(
    to right,
    rgba(56, 58, 144, 0.7),
    rgba(56, 58, 96, 0.95)
  ); */
  /* width: 30vw;
  height: 40vh;
  background-image: url("../assest/buddhist-kid.jpg");
