* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.image-section {
  background: url("../assest/buddha.jpg");
  background-position: center;
  background-size: cover;
  background-attachment: fixed;
  height: 100vh;
}
.content-on-img {
  width: 40%;
  padding: 70px;
  position: relative;
  top: 40%;
  margin: auto 50%;
}
.para-over {
  color: #fff;
  text-shadow: 2px 2px 6px rgb(29, 27, 27);
  line-height: 2em;
}
.about-us-section {
  display: flex;
  justify-content: space-evenly;
  margin: 5%;
}

.content-div {
  width: 50%;
  margin: 60px;
  text-align: justify;
  background: #86b3d1;
  box-shadow: 5px 5px 10px #b6b6b6;
  padding: 50px;
}
.content-div-two {
  width: 25%;
  margin: 60px;
  text-align: justify;
  background: #f7f9fb;
  box-shadow: 5px 5px 10px #b6b6b6;
  padding: 40px;
}
.content-div-three {
  width: 25%;
  margin: 60px;
  text-align: justify;
  background: #f7f9fb;
  box-shadow: 5px 5px 10px #b6b6b6;
  padding: 40px;
}
#about-sec {
  text-decoration: none;
  color: #000;
  font-size: 0.7em;
  padding: 10px;
  border-radius: 10px;
  /* box-shadow: -6px -6px 10px rgba(255, 255, 255, 0.8),
    6px 6px 10px rgba(0, 0, 0, 0.2); */
}
#about-sec:hover {
  /* box-shadow: inset -4px -4px 8px rgba(255, 255, 255, 0.5),
    inset 8px 8px 16px rgba(0, 0, 0, 0.1); */
    background-color: #000;
    color: #fff;
}
.image-div {
  background: url("../assest/monk-praying.jpg");
  height: 50vh;
  width: 40vw;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
.content-div h2 {
  margin-top: 5%;
}
iframe{     
  width:100%;
  height:600px;
  border:0px solid #fff;
}