.pooja-starting-container {
  display: grid;
  grid-template-columns: 0.7fr 3fr;
  grid-column-gap: 40px;
  grid-row-gap: 20px;
  margin: 8% 2%;
  /* grid-template-rows: 40vh 40vh 40vh; */
}
.container-first-vertical-1 {
  border-radius: 10px;
  padding: 20px;
  line-height: 2em;
  background: aliceblue;
}
.for-border {
  border-top: 2px solid black;
}
.container-first-vertical-2 {
  border-radius: 10px;
  background: aliceblue;
  padding: 10px;
  grid-row: 3/5;
  line-height: 2em;
}
.pooja-link {
  text-decoration: none;
  color: #000;
}
.pooja-link:hover {
  color: #f9f9f9;
  text-shadow: 2px 2px 5px #000000;
}
