* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background: #f3f0f1;
}
.navbar-section {
  background: #eae7dc;
  padding: 10px;
  position: fixed;
  z-index: 2;
  top: 0;
  width: 100%;
}
.navbar-details {
  display: flex;
  justify-content: space-between;
}
.link-section {
  margin: 10px;
}
#link {
  justify-content: center;
  border-radius: 20px;
  text-decoration: none;
  margin: auto 10px;
  padding: 15px;
  /* transition: all 2s ease-in; */
  color: #000;
  /* background: #f3f0f1; */
  /* box-shadow: -6px -6px 10px rgba(255, 255, 255, 0.8),
    6px 6px 10px rgba(0, 0, 0, 0.2); */
}
#link:hover {
  color: #eee2dc;
  background: #000;
}
/* #link:hover {
  box-shadow: inset -4px -4px 8px rgba(255, 255, 255, 0.5),
    inset 8px 8px 16px rgba(0, 0, 0, 0.1);
} */
.logo-section {
  border-radius: 20px;
  margin: auto 10px;
  padding: 5px;
  width: 80px;
  text-align: center;
  /* box-shadow: -6px -6px 10px rgba(255, 255, 255, 0.8),
    6px 6px 10px rgba(0, 0, 0, 0.2); */
}
.logo-section:hover {
  box-shadow: inset -4px -4px 8px rgba(255, 255, 255, 0.5),
    inset 8px 8px 16px rgba(0, 0, 0, 0.1);
}
