.cartoon-card {
  display: flex;
  justify-content: space-evenly;
  margin-top: 5%;
  margin-bottom: 5%;
}
.first-card {
  width: 25%;
  margin: auto;
  line-height: 1.8em;
  text-align: left;
  background: #fff;
  box-shadow: 0 0 45px #b4b4b4;
  padding: 30px;
}
.image-card-course {
  background: url("../assest/sta-bud.svg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  height: 40vh;
}
.main-container-course {
  padding: 10px;
}
.icons-left {
  padding: 20px;
  background: lightcoral;
}
.icons-right {
  padding: 20px;
  background: lightgreen;
}
.enroll-section {
  margin: 3% 2%;
}
#enroll-sec {
  border: 1px solid black;
  padding: 10px;
  color: #000;
  text-decoration: none;
}
#enroll-sec:hover {
  background: #171717;
  color: #fff;
}
